body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  background-color: #e2e2e2;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
/* Setup and visuallyHidden snippets are from the class notes */
article,aside,details,figcaption,figure,footer,header,hgroup,nav,section,summary{display:block;}audio,canvas,video{display:inline-block;}audio:not([controls]){display:none;height:0;}[hidden]{display:none;}html{font-family:sans-serif;-webkit-text-size-adjust:100%;-ms-text-size-adjust:100%;}a:focus{outline:thin dotted;}a:active,a:hover{outline:0;}h1{font-size:2em;}abbr[title]{border-bottom:1px dotted;}b,strong{font-weight:700;}dfn{font-style:italic;}mark{background:#ff0;color:#000;}code,kbd,pre,samp{font-family:monospace, serif;font-size:1em;}pre{white-space:pre-wrap;word-wrap:break-word;}q{quotes:\201C \201D \2018 \2019;}small{font-size:80%;}sub,sup{font-size:75%;line-height:0;position:relative;vertical-align:baseline;}sup{top:-.5em;}sub{bottom:-.25em;}img{border:0;}svg:not(:root){overflow:hidden;}fieldset{border:1px solid silver;margin:0 2px;padding:.35em .625em .75em;}button,input,select,textarea{font-family:inherit;font-size:100%;margin:0;}button,input{line-height:normal;}button,html input[type=button],/* 1 */input[type=reset],input[type=submit]{-webkit-appearance:button;cursor:pointer;}button[disabled],input[disabled]{cursor:default;}input[type=checkbox],input[type=radio]{box-sizing:border-box;padding:0;}input[type=search]{-webkit-appearance:textfield;box-sizing:content-box;}input[type=search]::-webkit-search-cancel-button,input[type=search]::-webkit-search-decoration{-webkit-appearance:none;}textarea{overflow:auto;vertical-align:top;}table{border-collapse:collapse;border-spacing:0;}body,figure{margin:0;}legend,button::-moz-focus-inner,input::-moz-focus-inner{border:0;padding:0;}

.clearfix:after {visibility: hidden; display: block; font-size: 0; content: ''; clear: both; height: 0; }

* { box-sizing: border-box; }

.visuallyHidden:not(:focus):not(:active) { position: absolute; width: 1px; height: 1px; margin: -1px;border: 0;padding: 0;white-space: nowrap;-webkit-clip-path: inset(100%);clip-path: inset(100%);clip: rect(00 0 0);overflow: hidden;}

/* Global styles start here */
body {
    font-size: 125%;
    font-family: 'Abril Fatface', cursive;
}

img {
    width: 100%;
    display: block;
}

.wrapper {
    width: 95%;
    margin: 0 auto;
}

p {
    padding: 0;
    margin: 0;
}

/* Header styles start here */
header {
    min-height: 100vh;
    letter-spacing: 8px;
    background-image: url(/project5/static/media/potterGlasses.b2bf8356.jpg); /* Photo by Larm Rmah on Unsplash */
    background-size: cover;
    background-position: center center;
    padding: 1px 0;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

h1 {
    padding: 20px 0;
    margin: 0;
    background-color: rgba(255,255,255,0.5);
    width: 100%;
    font-size: 2rem;
    text-shadow: 1px 1px rgba(0,0,0,0.5);
}

header p {
    background-color: rgba(255,255,255,0.5);
    font-size: 1.4rem;
    width: 100%;
    padding: 20px 0;
    text-shadow: 1px 1px rgba(0,0,0,0.5);
}

.fa-bolt {
    font-size: 2rem;
    -webkit-animation: flash 1.5s infinite;
            animation: flash 1.5s infinite;
    width: 75px;
    padding: 10px 0 10px 10px;
}

header button {
    margin-top: 65px;
    background-color: transparent;
    border: none;
    padding: 0;
}

button:hover {
    cursor: pointer;
}

header button:active {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}

header button:focus .fa-bolt {
    border: 2px dotted orange;
    border-radius: 15px;
}

@-webkit-keyframes flash {
    0% {
        color: rgba(255,255,255,0.5);
    }
    
    50% {
        color: rgba(255, 215, 0, 0.8);
        -webkit-transform: scale(1.4);
                transform: scale(1.4);
    }
    100% {
        color: rgba(255,255,255,0.5);
    }
}

@keyframes flash {
    0% {
        color: rgba(255,255,255,0.5);
    }
    
    50% {
        color: rgba(255, 215, 0, 0.8);
        -webkit-transform: scale(1.4);
                transform: scale(1.4);
    }
    100% {
        color: rgba(255,255,255,0.5);
    }
}
/* Form styles start here */
form {
    min-height: 75vh;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    background-image: url(/project5/static/media/greyBackground.e3d55104.png); /* Background image from https://www.toptal.com/designers/subtlepatterns/grey-jean/ */
    background-size: auto;
    background-position: center center;
}

h2 {
    background-color: rgba(255,255,255,0.5);
    padding: 20px 0;
    margin: 0;
    font-size: 1.8rem;
    letter-spacing: 8px;
    text-shadow: 1px 1px rgba(0,0,0,0.5);
}

.options {
    margin-top: 20px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}

form a {
    padding: 0;
    border: 3px groove rgb(255, 215, 0);
    width: calc((100% - 40px) / 2);
    margin: 10px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-transform: translateY(0);
            transform: translateY(0);
    -webkit-transition: -webkit-transform 0.15s ease-in-out;
    transition: -webkit-transform 0.15s ease-in-out;
    transition: transform 0.15s ease-in-out;
    transition: transform 0.15s ease-in-out, -webkit-transform 0.15s ease-in-out;
}

a:active {
    -webkit-transform: translateY(4px);
            transform: translateY(4px);
}

.gold {
    width: 100%;
    padding: 5px;
    border: 0;
    height: 80px;
    background-image: -webkit-linear-gradient(top left, #c6821d, #f6e67f, #c6821d);
    background-image: linear-gradient(to bottom right, #c6821d, #f6e67f, #c6821d);
    font-family: 'Abril Fatface', cursive;
    font-size: 1.2rem;
    position: relative;
}

/* This is for a shine effect on the buttons, code largely inspired by https://codepen.io/fishtoyo/pen/VYQVQx */
.gold:after {
	content: '';
	position: absolute;
    top: 0;
    left: 0;
	width: 0;
	height: 100%;
    background-color: rgba(255,255,255,0.7);
}

.gold:hover:after {
    width: 100%;
    background-color: rgba(255,255,255,0);
	-webkit-transition: all 0.75s ease-in-out;
	transition: all 0.75s ease-in-out;
}

.gold:focus {
    border: 3px groove grey;
}

@media (min-width: 750px) {
    .options {
        width: 75%;
        max-width: 800px;
        margin: 0 auto;
    }
}
/* Results section styles start here */
.results {
    min-height: 75vh;
    background-image: -webkit-linear-gradient(top, rgba(255,255,255,0.3), rgba(255,255,255,0.3)), url(/project5/static/media/magicWand.a4eef9d4.jpg);
    background-image: linear-gradient(to bottom, rgba(255,255,255,0.3), rgba(255,255,255,0.3)), url(/project5/static/media/magicWand.a4eef9d4.jpg); /* Photo by Emy Girard on Unsplash */
    background-size: cover;
    background-position: center center;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
}

.results h2 {
    background-color: transparent;
    padding: 0;
}

.resultsBox {
    background-color: rgba(255,255,255,0.5);
    padding: 20px 0;
}

.randomSpellName {
    font-size: 2.2rem;
    padding-top: 40px;
    margin: 0;
    text-shadow: 1px 1px rgba(0,0,0,0.5);
}

.randomSpellEffect {
    font-size: 1rem;
    -webkit-animation: fade-in 0.5s linear;
            animation: fade-in 0.5s linear;
    padding: 20px 0 40px 0;
}

.results h3 {
    padding: 0;
    padding-bottom: 20px;
    margin: 0;
    letter-spacing: 3px;
    text-shadow: 1px 1px rgba(0,0,0,0.5);
}

.results button {
    width: 60%;
    max-width: 215px;
    height: 69px;
    border: 3px ridge rgb(255, 215, 0);
    -webkit-transform: translateY(0);
            transform: translateY(0);
    -webkit-transition: -webkit-transform 0.15s ease-in-out;
    transition: -webkit-transform 0.15s ease-in-out;
    transition: transform 0.15s ease-in-out;
    transition: transform 0.15s ease-in-out, -webkit-transform 0.15s ease-in-out;
}

.results button:active {
    -webkit-transform: translateY(4px);
            transform: translateY(4px);
}

.results button:focus {
    border: 3px groove grey;
}

.results .newPlace {
    margin-top: 20px;
}

@media (min-width: 750px) {
    .links {
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: row;
                flex-direction: row;
        -webkit-justify-content: space-around;
                justify-content: space-around;
        -webkit-flex: 1 1;
                flex: 1 1;
    }

    .links > * {
        width: 38%;
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: column;
                flex-direction: column;
        -webkit-justify-content: space-between;
                justify-content: space-between;
        -webkit-align-items: center;
                align-items: center;
    }

    .results .newPlace {
        margin: 0;
    }

    .results .newPlace button {
        width: 100%;
    }
}
/* Footer styles start here */
footer {
    background-color: rgb(45, 41, 41);
}

footer p {
    font-size: 1rem;
    font-family: 'Abel', sans-serif;
    color: #e2e2e2;
    padding: 20px 0;
    margin: 0;
}

footer a {
    color: rgb(255, 215, 0);
    text-decoration: none;
}

footer a:visited {
    color: rgb(255, 215, 0);
}
